.App {
  background-color: #e5e5e5;
  /* #c6dfce */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #4C433D;
}

.banner {
  height: 100vh;
  padding-bottom: 250px;
  text-align: center;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* NavBar component */

.nav-icon-div {
  position: fixed; 
  top: 10px;
  right: 10px;
  z-index: 100;
}

.file-icon {
  width: 30px;
}

.nav-icon {
  font-size: 2.5rem;
  margin: 3px;
  color: #ffffff;
  filter: drop-shadow(0 0 5px #555454);
}

.nav-icon:hover {
  filter: drop-shadow(0 0 10px #ffffff)
}

.github-icon:hover {
  color: #000000;
}

.linkedin-icon:hover {
  color: #0072b1;
}

.resume-icon:hover {
  color: #31ae40;
}


.bottom-div {
  position: fixed; 
  bottom: 10px;
  right: 10px;
  z-index: 100;
}

.top-icon {
  font-size: 3.5rem;
  transition-duration: 0.3s;
  padding: 5px;
  color: #ffffff;
  filter: drop-shadow(0 0 5px #555454);
}

.top-icon:hover {
  filter: drop-shadow(0 0 10px #ffffff);
  color: #ffffff;
}

@media screen and (max-width: 690px) {
  .nav-icon-div {
    position: absolute; 
    top: 10px;
    right: 10px;
    z-index: 100;
  }
}



/* Skills component */

.skills-div {
  text-align: center;
  color: white;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 0 10px;
}

.skills-icon-div {
  font-size: 4rem;
  text-shadow: 0 0 8px #000000;
  transition-duration: 0.3s;
  padding: 10px;
}

.skills-icon-div:hover {
  padding-top: 0;
  text-shadow: 0 0 20px #ffffff;
  transition-duration: 0.3s;
}

.html-icon:hover {
  color: #e34c26;
}

.css-icon:hover {
  color: #264de4;
}

.javascript-icon:hover {
  color: #F0DB4F;
}

.react-icon:hover {
  color: #61dbfb;
}

.python-icon:hover {
  color: #4584b6;
}

.django-icon:hover {
  color: #092e20;
}

.postgresql-icon:hover {
  color: #008bb9;
}

.mongodb-icon:hover {
  color: #589636;
}

.nodejs-icon:hover {
  color: #3C873A;
}

.bootstrap-icon:hover {
  color: #563d7c;
}

.skills-icon {
  visibility: hidden;
  font-size: 1rem;
  margin-top: -5px;
  font-family: sans-serif;
}

.skills-icon-div:hover .skills-icon {
  visibility: visible;
  font-size: 1rem;
  color: white;
  text-shadow: 0 0 10px #000000;
}


/* About me component */

.about-me {
  color: white;
  border-top: 2px solid #fefde5;
  border-bottom: 3px solid #6c58b2;
  border-radius: 12px;
  box-shadow: 0 0 13px #3e4497;
  background-image: linear-gradient(#85a4ee, #8081eb);
  margin: 20px 0 80px 0;
  padding-bottom: 40px;
}

.about-me-div {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: -20px;
}


.about-me-desc {
  width: 30rem;
  text-align: center;
  font-size: 1.5rem;
}

